<template>
  <div class="chart-container">
    <h5 class="chart-title">
      用户行为统计
      <el-popover :content="$slots.default ? $slots.default[0].text : '统计本活动内，当前筛选条件下各用户行为发行积分与回收积分数量的分布情况'">
        <el-link :underline="false" icon="el-icon-question" slot="reference"></el-link>
      </el-popover>
    </h5>

    <div class="chart-main">
      <div class="chart">
        <div class="total"><span>{{ total }}</span>总计</div>
        <v-chart :options="options"></v-chart>
      </div>
      <div class="table">
        <el-table
          :data="datas"
          size="small"
          show-summary
          :summary-method="getSummaries"
          style="width: 100%">
          <el-table-column prop="name" label="">
            <template slot-scope="scope">
              <span class="colorTag"><i></i>{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="integral" label="积分">
            <template slot-scope="scope">{{ scope.row.integral | fmtMoney }}</template>
          </el-table-column>
          <el-table-column label="占比">
            <template slot-scope="scope">{{ (scope.row.integral / total) | fmtFloat2Percent }}</template>
          </el-table-column>
          <el-table-column prop="lastIntegral" label="去年同期">
            <template slot-scope="scope">{{ scope.row.lastIntegral | fmtMoney }}</template>
          </el-table-column>
          <el-table-column prop="lastProportion" label="去年同期占比">
            <template slot-scope="scope">{{ scope.row.lastProportion | fmtFloat2Percent }}</template>
          </el-table-column>
          <el-table-column label="同比增长">
            <template slot-scope="scope">
              <span>{{ (Math.abs((scope.row.integral - scope.row.lastIntegral)) / scope.row.lastIntegral) | fmtFloat2Percent }}<arrow-icon :value="scope.row.integral - scope.row.lastIntegral" /></span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import pie from './common/pie'

export default {
  name: 'chart-user-behavior',
  mixins: [pie],
  data () {
    return {
      options: {
        tooltip: {
          formatter: '{b}<br/>积分：{c}<br/>占比：{d}%'
        },
        series: [
          {
            name: '用户行为统计',
            type: 'pie',
            radius: ['60%', '90%'],
            label: { show: false },
            labelLine: { show: false },
            data: []
          }
        ]
      }
    }
  },
  methods: {
    isMatchCondition (column) {
      return column.property === 'integral' || column.property === 'lastIntegral'
    }
  }
}
</script>

