<template>
  <span class="sts-icon" :class="status | fmtStatusColor" :style="{ marginLeft: point ? '20px' : 0 }">
    <i v-if="point && status !== undefined"></i>{{ status | fmtStatus }}
  </span>
</template>

<script>
const STATUS = {
  '00': '未开始',
  10: '已开始',
  11: '已重启',
  20: '已暂停',
  30: '已停用',
  40: '已结束'
}

export default {
  name: 'rule-sts-icon',
  props: {
    status: [Number, String],
    point: Boolean
  },
  filters: {
    // 状态文案
    fmtStatus (val) {
      return val === undefined ? '' : (STATUS[val] ? STATUS[val] : '未知')
    },

    // 状态颜色
    fmtStatusColor (val, type = 'color') {
      switch (STATUS[val]) {
        case '未开始':
        case '已结束': return 'color-black'
        case '已开始':
        case '已重启': return 'color-green'
        case '已停用':
        case '已暂停': return 'color-red'
        default: return 'color-black'
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import './stsIcon.scss';
</style>
