<template>
  <div class="activity-detail" v-loading="isLoading">
    <template v-if="$route.matched.length === 3">
      <el-row :gutter="gutter">
        <!-- 基本信息 -->
        <el-col :span="18">
          <el-new-card>
            <template slot="header">
              <div>{{ $route.meta.name }}<act-sts-icon :status="info.status" point /></div>
              <el-button size="small" plain icon="el-icon-s-operation" @click="popupCtrlPanel">活动控制面板</el-button>
            </template>
