              <info-item label="担当：">{{ info.contact }}</info-item>
              <info-item label="业务归属：">{{ info.busType }}</info-item>
              <info-item label="活动模式：">{{ info.mode }}</info-item>
              <info-item label="活动类型：">{{ info.type }}</info-item>
              <info-item label="平台：">{{ info.plat }}</info-item>
              <info-item label="开始时间：">{{ info.startTime | fmtDateTime }}</info-item>
              <info-item label="结束时间：">{{ info.endTime | fmtDateTime }}</info-item>
              <info-item label="累计活动天数：">{{ info.days }}</info-item>
              <info-item label="活动简介：" block>{{ info.intro }}</info-item>
            </info-block>
            <div class="btns-box">
              <el-button icon="el-icon-s-order" size="small" @click="popAttachments">查看附件</el-button>
              <span>
                <el-button size="small" plain icon="el-icon-document" @click="toReport">查看报表</el-button>
                <el-button size="small" plain icon="el-icon-notebook-2" @click="toCrtdDetail">查看明细</el-button>
              </span>
            </div>
          </el-new-card>
        </el-col>

        <!-- 待办事项 -->
        <el-col :span="6">
          <to-do-list :list="toDo" height="232px"></to-do-list>
        </el-col>
      </el-row>

      <!-- 四大比较数据 -->
      <today-data :gutter="gutter" :data="todayData"></today-data>

      <el-row :gutter="gutter">
        <!-- 额度概览 -->
        <el-col :span="18">
          <el-new-card>
            <template slot="header">
              <div>
                积分池概览
                <span class="acct-sts" :style="2 | fmtAcctApplStatusColor">
                  <i :style="2 | fmtAcctApplStatusColor('backgroundColor')"></i>
                  提额申请中
                </span>
              </div>
            </template>
            <amount-act :data="amountData"></amount-act>
            <div class="btns-box">
              <span>
                <el-button size="small" plain icon="el-icon-document-copy" @click="popupChgRec">额度变更记录</el-button>
                <el-button size="small" plain icon="el-icon-tickets" @click="popupUseRec">备用额度使用申请记录</el-button>
              </span>
              <span v-if="isEditor">
                <el-button size="small" plain icon="el-icon-document-remove" @click="popupUnissue">申请处理未发行额度</el-button>
                <el-button size="small" plain icon="el-icon-document-checked" @click="popupUseImp">申请使用备用额度</el-button>
                <el-button size="small" plain icon="el-icon-document-add" @click="popupAddAmt">申请额度</el-button>
              </span>
            </div>
          </el-new-card>
        </el-col>

        <!-- 积分发行量排行 -->
        <el-col :span="6">
          <el-new-card>
            <template slot="header">积分发行量排行</template>

            <div class="issueAmount-list">
              <div class="issueAmount-item" v-for="(item, index) in issueList" :key="'item' + index">
                <div>
                  <span><i>{{ index + 1 + '. ' }}</i>{{ item.name }}</span>
                  <span>{{ item.issue }}</span>
                  <span>{{ item.percent | fmtFloat2Percent }}</span>
                </div>
                <div class="issueAmount-bar">
                  <span :style="{ width: Math.round(item.percent * 100) + '%' }"></span>
                </div>
              </div>
            </div>
          </el-new-card>
        </el-col>
      </el-row>

      <!-- 统计筛选条件 -->
      <el-new-card>
        <el-form inline :model="searchForm" size="small" class="condition-bar">
          <el-form-item>
            <el-select v-model="searchForm.behavior">
              <el-option label="全部" :value="1"></el-option>
              <el-option label="已开始" :value="2"></el-option>
              <el-option label="已结束" :value="3"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-select v-model="searchForm.verifiSts" placeholder="认证状态">
              <el-option label="全部" value=""></el-option>
              <el-option label="已注册" :value="1"></el-option>
              <el-option label="未注册" :value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <date-range v-model="dateRange"></date-range>
          </el-form-item>
          <el-form-item>
            <el-button plain type="primary" :loading="isChartsLoading" @click="searchData" style="min-width: 75px;">查询</el-button>
          </el-form-item>
        </el-form>
      </el-new-card>

      <!-- 图表统计查询结果 -->
      <el-tabs type="border-card" v-if="pointsData.pointsIssued">
        <el-tab-pane label="用户行为统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-user-behavior :datas="userBehaviorData"></chart-user-behavior>
            <el-divider></el-divider>
            <chart-points-issued :datas="pointsData.pointsIssued">统计本活动内，当前筛选条件下各用户行为发行积分数量的分布情况</chart-points-issued>
            <el-divider></el-divider>
            <chart-points-recyled :datas="pointsData.pointsRecyled">统计本活动内，当前筛选条件下各用户行为回收积分数量的分布情况</chart-points-recyled>
            <el-divider></el-divider>
            <chart-points-chain :datas="pointsData.pointsChain">统计本次活动内，当前筛选条件下的已发行、已回收的环比统计情况</chart-points-chain>
            <el-divider></el-divider>
            <chart-points-growth-rate :datas="pointsData.pointsGrowthRate">统计本次活动内，当前筛选条件下的已发行、已回收的增长率</chart-points-growth-rate>
            <el-divider></el-divider>
            <chart-points-recyled-src :datas="pointsData.pointsRecyledSrc">统计本次活动内，已回收积分在各部门的分布情况</chart-points-recyled-src>
          </div>
        </el-tab-pane>
        <el-tab-pane label="活动效果统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-part-user :datas="activityEffectData.partUser">统计本次活动内，当前筛选条件下所有用户行为的参与用户数统计</chart-part-user>
            <el-divider></el-divider>
            <chart-part-user-chain :datas="activityEffectData.partUserChain">统计本次活动内，当前筛选条件下各用户行为的参与用户数的环比与增长率</chart-part-user-chain>
            <el-divider></el-divider>
            <chart-part-times :datas="activityEffectData.partTimes">统计本次活动内，当前筛选条件下各用户行为的参与次数统计</chart-part-times>
            <el-divider></el-divider>
            <chart-part-times-chain :datas="activityEffectData.partTimesChain">统计本次活动内，当前筛选条件下各活动用户参与次数的环比与增长率</chart-part-times-chain>
          </div>
        </el-tab-pane>
        <el-tab-pane label="结算统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-stmt-overview :datas="settlementData.overview">统计在当前筛选条件下所有由本活动发行的积分，已核销部分在各结算状态下的分布情况</chart-stmt-overview>
            <el-divider></el-divider>
            <chart-stmt-sts-rec :datas="settlementData.stsRec">统计在当前筛选条件下所有由本次活动发行的积分，已核销应收部分在各结算状态下的分布情况</chart-stmt-sts-rec>
            <el-divider></el-divider>
            <chart-stmt-sts-pay :datas="settlementData.stsPay">统计在当前筛选条件下所有由本次活动发行的积分，已核销应付部分在各结算状态下的分布情况</chart-stmt-sts-pay>
            <el-divider></el-divider>
            <chart-stmt-dept-rec :datas="settlementData.deptRec">统计在当前筛选条件下所有由本次活动发行的积分，已核销应收部分在各部门下的分布情况</chart-stmt-dept-rec>
            <el-divider></el-divider>
            <chart-stmt-dept-pay :datas="settlementData.deptPay">统计在当前筛选条件下所有由本次活动发行的积分，已核销应付部分在各部门下的分布情况</chart-stmt-dept-pay>
          </div>
        </el-tab-pane>
        <el-tab-pane label="商品统计" lazy>
          <div class="charts-header">{{ fmtCDateRange }}<el-link :underline="false" icon="el-icon-download"></el-link></div>
          <div class="charts-box">
            <chart-goods :datas="goodsData.goods">统计本次活动发行的积分，当前筛选条件下兑换商品的情况</chart-goods>
            <el-divider></el-divider>
            <chart-goods-chain :datas="goodsData.goodsChain">统计本次活动发行的积分，当前筛选条件下兑换商品的环比情况</chart-goods-chain>
          </div>
        </el-tab-pane>
        <el-tab-pane label="业务规则" lazy class="rule-parts">
          <h5 class="sub-title">活动整体规则配置<el-button size="small" icon="el-icon-plus">新建</el-button></h5>
          <el-table
            :data="info.wholeRules"
            stripe
            size="small"
            style="width: 100%">
            <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
            <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
            <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
            <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            <el-table-column prop="status" label="状态" width="100px">未开始</el-table-column>
            <el-table-column label="操作" align="center" width="150px">
              <template slot-scope="scope">
                <el-link type="danger" icon="el-icon-video-pause" @click="stopRule(scope.row.id)">停用</el-link>
              </template>
            </el-table-column>
          </el-table>

          <h5 class="sub-title">用户行为规则配置 - 发行<el-button size="small" icon="el-icon-plus">新建</el-button></h5>
          <template v-for="r in info.behaviorIssueRules">
            <h5 class="sub-title" :key="'issue-title' + r.id" style="margin-top: 40px;">
              <span>{{ r.name }}<rule-sts-icon :status="1" point /></span>
              <div>
                <el-button size="small" plain type="danger" icon="el-icon-video-pause" @click="stopRule(r.id)">停用</el-button>
                <div>
                  <div><strong>开始时间: </strong>活动同步</div>
                  <div><strong>结束时间: </strong>活动同步</div>
                </div>
              </div>
            </h5>
            <el-table
              :data="r.rules"
              stripe
              size="small"
              :key="'issue-table' + r.id"
              style="width: 100%">
              <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
              <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
              <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
              <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
              <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            </el-table>
          </template>

          <h5 class="sub-title">用户行为规则配置 - 核销<el-button size="small" icon="el-icon-plus">新建</el-button></h5>
          <template v-for="r in info.behaviorVerifiRules">
            <h5 class="sub-title" :key="'issue-title' + r.id" style="margin-top: 40px;">
              <span>{{ r.name }}<rule-sts-icon :status="5" point /></span>
              <div>
                <div>
                  <div><strong>开始时间: </strong>活动同步</div>
                  <div><strong>结束时间: </strong>活动同步</div>
                </div>
              </div>
            </h5>
            <el-table
              :data="r.rules"
              stripe
              size="small"
              :key="'verifi-table' + r.id"
              style="width: 100%">
              <el-table-column prop="rid" label="规则ID" align="center" width="120px"></el-table-column>
              <el-table-column prop="name" label="规则名称" min-width="130px"></el-table-column>
              <el-table-column prop="desc" label="规则说明" min-width="150px"></el-table-column>
              <el-table-column prop="startTime" label="开始时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
              <el-table-column prop="endTime" label="结束时间" align="center" width="150px">{{ new Date() | fmtDateTime }}</el-table-column>
            </el-table>
          </template>
        </el-tab-pane>
      </el-tabs>

      <popup-lmt-chg-rec :visible.sync="chgRecVisible" :id="id" @popupAct="popupActIntro" @popupDtl="popupUnissueDetail"></popup-lmt-chg-rec>
      <popup-use-imp-rec :visible.sync="useRecVisible" :id="id" @popupDtl="popupUseImpDetail"></popup-use-imp-rec>
      <popup-act-intro :visible.sync="actIntroVisible" :aid="selectedId"></popup-act-intro>
      <popup-add-amt-detail :visible.sync="addAmtDtlVisible" :sid="selectedId" @popupEdit="popupAddAmt"></popup-add-amt-detail>
      <popup-use-imp-detail :visible.sync="useImpDtlVisible" :sid="selectedId" @popupEdit="popupUseImp"></popup-use-imp-detail>
      <popup-unissue-detail :visible.sync="unissueDtlVisible" :sid="selectedId" @popupEdit="popupUnissue"></popup-unissue-detail>
      <popup-add-amt :visible.sync="addAmtVisible" :sid="selectedId"></popup-add-amt>
      <popup-use-imp :visible.sync="useImpVisible" :sid="selectedId"></popup-use-imp>
      <popup-unissue :visible.sync="unissueVisible" :sid="selectedId"></popup-unissue>
      <control-panel :visible.sync="ctrlPanelVisible" :id="id" :status="info.status"></control-panel>
      <popup-atta :visible.sync="attaVisible" :fileList="info.attachments"></popup-atta>
    </template>

    <router-view v-else />
  </div>
</template>

<script>
import ElNewCard from '@/components/ElNewCard'
import TodayData from '@/components/TodayData'
import InfoBlock from '@/components/InfoBlock'
import InfoItem from '@/components/InfoItem'
import AmountAct from '@/components/amounts/AmountAct'
import ActStsIcon from '@/components/status/ActStsIcon'
import AcctStsIcon from '@/components/status/AcctStsIcon'
import RuleStsIcon from '@/components/status/RuleStsIcon'
import ToDoList from '@/components/ToDoList'
import DateRange from '@/components/DateRange'
import chartMixins from '@/components/charts/mixins'
import moment from 'moment'
import PopupLmtChgRec from '@/views/popups/PopupLmtChgRec'
import PopupUseImpRec from '@/views/popups/PopupUseImpRec'
import PopupActIntro from '@/views/popups/PopupActIntro'
import PopupAddAmtDetail from '@/views/popups/PopupAddAmtDetail'
import PopupUseImpDetail from '@/views/popups/PopupUseImpDetail'
import PopupUnissueDetail from '@/views/popups/PopupUnissueDetail'
import PopupAddAmt from '@/views/popups/PopupAddAmt'
import PopupUseImp from '@/views/popups/PopupUseImp'
import PopupUnissue from '@/views/popups/PopupUnissue'
import ControlPanel from './ControlPanel'
import PopupAtta from '@/views/popups/PopupAtta'
import { mapGetters } from 'vuex'
import UserBehavior from '@/components/charts/userBehavior'

export default {
  name: 'account-detail',
  mixins: [chartMixins],
  components: {
    ElNewCard,
    TodayData,
    InfoBlock,
    InfoItem,
    AmountAct,
    ActStsIcon,
    AcctStsIcon,
    RuleStsIcon,
    ToDoList,
    DateRange,
    PopupLmtChgRec,
    PopupUseImpRec,
    PopupActIntro,
    PopupAddAmtDetail,
    PopupUseImpDetail,
    PopupUnissueDetail,
    PopupAddAmt,
    PopupUseImp,
    PopupUnissue,
    ControlPanel,
    PopupAtta,
    ChartUserBehavior: UserBehavior
  },
  data () {
    return {
      id: 0, // 当前活动id
      gutter: 40, // 面板之间的间距
      amountData: {}, // 额度概览
      toDo: [], // 待办事项
      todayData: [], // 本日数据
      searchForm: { // 搜索条件
        status: null,
        verifiSts: null,
        startTime: null,
        endTime: null
      },
      dateRange: [], // 搜索日期范围
      isLoading: false,
      isChartsLoading: false,
      fmtCDateRange: '', // 统计图表统计时间格式化展示
      info: {}, // 活动信息
      issueList: [ // 积分发行量排行
        {
          id: 1,
          name: '签到',
          issue: 62345,
          percent: 0.65
        },
        {
          id: 2,
          name: '点赞',
          issue: 52345,
          percent: 0.55
        },
        {
          id: 3,
          name: '注册',
          issue: 42345,
          percent: 0.45
        },
        {
          id: 4,
          name: '发帖',
          issue: 32345,
          percent: 0.35
        },
        {
          id: 5,
          name: '评论',
          issue: 22345,
          percent: 0.25
        }
      ],
      selectedId: 0,
      chgRecVisible: false,
      useRecVisible: false,
      actIntroVisible: false,
      addAmtDtlVisible: false,
      useImpDtlVisible: false,
      unissueDtlVisible: false,
      addAmtVisible: false,
      useImpVisible: false,
      unissueVisible: false,
      ctrlPanelVisible: false,
      attaVisible: false,
      userBehaviorData: ['已发行', '已回收'].map((a, idx) => {
        return {
          name: a,
          integral: 275800,
          proportion: 0.33,
          lastIntegral: 270000,
          lastProportion: 0.33
        }
      })
    }
  },
  computed: {
    ...mapGetters({
      accountInfo: 'getAccountInfo'
    }),

    isEditor () {
      return this.info.deptId === this.accountInfo.orgId
    },

    isGTMC () {
      return this.accountInfo.orgId === 1
    }
  },
  watch: {
    dateRange: function (val) {
      this.searchForm.startTime = val[0]
      this.searchForm.endTime = val[1]
    },

    isChartsLoading: function (val) {
      if (!val) {
        const startTime = this.dateRange[0] ? moment(this.dateRange[0]).format('YYYY年MM月DD日 HH时mm分') : ''
        const endTime = moment(this.dateRange[1] ? this.dateRange[1] : moment()).format('YYYY年MM月DD日 HH时mm分')
        this.fmtCDateRange = startTime + ' 至 ' + endTime
      }
    }
  },
  created () {
    this.id = this.$route.query.id
    if (this.id) {
      this.getData(this.id)
    } else {
      this.$router.replace('/404')
    }
  },
  methods: {
    // 获取详情信息
    getData (id) {
      this.isLoading = true
      this.$http.serviceGetActDetail({ id })
        .then(res => {
          this.info = res.info
          this.amountData = res.amountData
          this.todayData = res.todayData
          this.toDo = res.toDoList
        })
        .finally(() => {
          this.isLoading = false
        })
    },

    // 跳转到查看报表
    toReport () {
      this.$router.push({
        path: '/activityMgmt/actDetail/actReport',
        query: {
          id: this.$route.query.id
        }
      })
    },

    // 跳转到查看明细页面
    toCrtdDetail () {
      this.$router.push({
        path: '/activityMgmt/actDetail/actCrdtDetail',
        query: {
          id: this.$route.query.id
        }
      })
    },

    // 弹出额度变更记录弹窗
    popupChgRec () {
      this.chgRecVisible = true
    },

    // 弹出备用额度使用申请记录弹窗
    popupUseRec () {
      this.useRecVisible = true
    },

    // 弹出活动简介弹窗
    popupActIntro (aid) {
      this.selectedId = aid
      this.actIntroVisible = true
    },

    // 弹出提额申请详情
    popupAddAmtDetail (aid) {
      this.selectedId = aid
      this.addAmtDtlVisible = true
    },

    // 弹出备用使用申请详情
    popupUseImpDetail (aid) {
      this.selectedId = aid
      this.useImpDtlVisible = true
    },

    popupUnissueDetail (aid) {
      this.selectedId = aid
      this.unissueDtlVisible = true
    },

    // 弹出申请额度弹窗
    popupAddAmt (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.addAmtVisible = true
    },

    // 弹出备用额度使用申请弹窗
    popupUseImp (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.useImpVisible = true
    },

    // 弹出未发行额度处理申请弹窗
    popupUnissue (applId) {
      this.selectedId = typeof applId !== 'object' ? applId : null
      this.unissueVisible = true
    },

    // 查看附件
    popAttachments () {
      this.attaVisible = true
    },

    // 弹出控制面板
    popupCtrlPanel () {
      this.ctrlPanelVisible = true
    },

    // 查询统计数据
    searchData () {
      this.isChartsLoading = true
      this.$http.serviceChartsData(this.searchForm)
        .then(res => {
          this.pointsData = res.pointsData
          this.bussinessData = res.bussinessData
          this.activityEffectData = res.activityEffectData
          this.activityTypeData = res.activityTypeData
          this.settlementData = res.settlementData
          this.goodsData = res.goodsData
        })
        .finally(() => {
          this.isChartsLoading = false
        })
    },

    // 处理活动名下拉复选的全选问题
    searchNameChange (val) {
      if (val.some(v => v === '')) {
        this.searchForm.name = ['']
      }
    },

    stopRule (id) {
      this.$confirm('是否确认停用该规则？', { type: 'error' })
    }
  }
}
</script>

